.container{
    width: 600px;
    height: 800px;
    margin: auto;
    background-color: aqua;
    margin-top: 75px;
    margin-bottom: 75px;
    border-radius: 12px;
}
.search-bar{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
    padding-top: 60px;
}
.search-bar input{
    border: none;
    display: flex;
    height: 78px;
    width: 360px;
    background: #ffff;
    border-radius: 40px;
    padding-left: 40px;
    color: #626262;
    outline: none;
    font-size: 20px;
    font-weight: 400;
}
.search-icon{
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 40px;
    width: 78px;
    height: 78px;
    cursor: pointer;
    background-color: #ffff;

}